import React from "react";
import "./Header.css";

function Header() {
  return (
    <div className="header">
      <a href = '/' src = 'Home'>
        <img 
        className="header_img_logo"
        src="data:image/jpeg;base64,"
        alt="logo"
      />
      </a>
      
    </div>
  );
}

export default Header;
